___CSS_LOADER_EXPORT___.push([module.id, "#nuevo-participante-dialog .mdc-dialog__surface,#participante-dialog .mdc-dialog__surface{max-width:1050px}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Convocatorias/Proyectos/Participantes/Participantes.svelte"],"names":[],"mappings":"AAqcY,0FAA6F,gBAChF","sourcesContent":["<script>\r\n    import { Inertia } from '@inertiajs/inertia'\r\n    import { useForm, page } from '@inertiajs/inertia-svelte'\r\n    import { route, checkRole, checkPermission } from '@/Utils'\r\n    import { _ } from 'svelte-i18n'\r\n    import axios from 'axios'\r\n\r\n    import Input from '@/Shared/Input'\r\n    import Label from '@/Shared/Label'\r\n    import Select from '@/Shared/Select'\r\n    import LoadingButton from '@/Shared/LoadingButton'\r\n    import DataTableMenu from '@/Shared/DataTableMenu'\r\n    import { Item, Text } from '@smui/list'\r\n    import Dialog from '@/Shared/Dialog'\r\n    import Button from '@/Shared/Button'\r\n    import DynamicList from '@/Shared/Dropdowns/DynamicList'\r\n\r\n    export let errors\r\n    export let convocatoria\r\n    export let proyecto\r\n    export let tiposDocumento\r\n    export let tiposParticipacion\r\n    export let roles\r\n\r\n    let resultados = []\r\n\r\n    /**\r\n     * Permisos\r\n     */\r\n    let authUser = $page.props.auth.user\r\n    let isSuperAdmin = checkRole(authUser, [1])\r\n\r\n    /**\r\n     * Buscar\r\n     */\r\n    let form = useForm({\r\n        search_participante: '',\r\n    })\r\n    let formID\r\n    let dialogOpen = false\r\n    let dialogTitle\r\n    let sending = false\r\n    let sended = false\r\n    function submit() {\r\n        sending = true\r\n        sended = false\r\n        try {\r\n            axios\r\n                .post(route('convocatorias.proyectos.participantes.users', { convocatoria: convocatoria.id, proyecto: proyecto.id }), $form)\r\n                .then((response) => {\r\n                    resultados = response.data\r\n                    sending = false\r\n                    sended = true\r\n                })\r\n                .catch((error) => {\r\n                    sending = false\r\n                })\r\n        } catch (error) {\r\n            sending = false\r\n        }\r\n    }\r\n\r\n    function removeParticipante(id) {\r\n        if (isSuperAdmin || checkPermission(authUser, [1, 3, 4, 5, 6, 7, 8, 9, 10])) {\r\n            Inertia.post(\r\n                route('convocatorias.proyectos.participantes.users.unlink', {\r\n                    proyecto: proyecto.id,\r\n                    convocatoria: convocatoria.id,\r\n                }),\r\n                { user_id: id, _method: 'DELETE' },\r\n                { preserveScroll: true },\r\n            )\r\n        }\r\n    }\r\n\r\n    /**\r\n     * Participantes\r\n     */\r\n    let formParticipante = useForm({\r\n        _method: null,\r\n        user_id: 0,\r\n        es_autor: 0,\r\n        cantidad_horas: 0,\r\n        cantidad_meses: 0,\r\n        rol_id: null,\r\n    })\r\n\r\n    function showParticipante(user) {\r\n        reset()\r\n        dialogTitle = user.nombre\r\n        dialogOpen = true\r\n        formID = 'participante-form'\r\n        $formParticipante.user_id = user.id\r\n        if (user.pivot) {\r\n            $formParticipante._method = 'PUT'\r\n            $formParticipante.es_autor = user.pivot.es_autor ? 1 : 0\r\n            $formParticipante.cantidad_meses = user.pivot.cantidad_meses\r\n            $formParticipante.cantidad_horas = user.pivot.cantidad_horas\r\n            $formParticipante.rol_id = { value: user.pivot.rol_id, label: roles.find((item) => item.value == user.pivot.rol_id)?.label }\r\n        }\r\n    }\r\n\r\n    function submitParticipante() {\r\n        if (isSuperAdmin || checkPermission(authUser, [1, 3, 4, 5, 6, 7, 8, 9, 10])) {\r\n            $formParticipante.post(\r\n                route('convocatorias.proyectos.participantes.users.link', {\r\n                    proyecto: proyecto.id,\r\n                    convocatoria: convocatoria.id,\r\n                }),\r\n                {\r\n                    onStart: () => {\r\n                        sending = true\r\n                    },\r\n                    onSuccess: () => {\r\n                        closeDialog()\r\n                    },\r\n                    onFinish: () => {\r\n                        sending = false\r\n                    },\r\n                    preserveScroll: true,\r\n                },\r\n            )\r\n        }\r\n    }\r\n\r\n    /**\r\n     * Registrar nuevo participante\r\n     */\r\n    let formNuevoParticipante = useForm({\r\n        nombre: '',\r\n        email: '',\r\n        tipo_documento: '',\r\n        numero_documento: '',\r\n        numero_celular: '',\r\n        tipo_participacion: '',\r\n        es_autor: 0,\r\n        cantidad_meses: 0,\r\n        cantidad_horas: 0,\r\n        centro_formacion_id: null,\r\n        rol_id: null,\r\n    })\r\n\r\n    let formNuevoParticipanteId\r\n    let openNuevoParticipanteDialog = false\r\n    function showRegister() {\r\n        reset()\r\n        openNuevoParticipanteDialog = true\r\n        formNuevoParticipanteId = 'nuevo-participante-form'\r\n    }\r\n\r\n    function submitRegister() {\r\n        if (isSuperAdmin || checkPermission(authUser, [1, 3, 4, 5, 6, 7, 8, 9, 10])) {\r\n            $formNuevoParticipante.post(route('convocatorias.proyectos.participantes.users.register', { convocatoria: convocatoria.id, proyecto: proyecto.id }), {\r\n                onStart: () => {\r\n                    sending = true\r\n                },\r\n                onSuccess: () => {\r\n                    closeDialog()\r\n                },\r\n                onFinish: () => {\r\n                    sending = false\r\n                },\r\n                preserveScroll: true,\r\n            })\r\n        }\r\n    }\r\n\r\n    function reset() {\r\n        //Participante - form\r\n        $formParticipante.reset()\r\n        //Nuevo participante - form\r\n        $formNuevoParticipante.reset()\r\n    }\r\n\r\n    function closeDialog() {\r\n        reset()\r\n        dialogOpen = false\r\n        openNuevoParticipanteDialog = false\r\n        sending = false\r\n    }\r\n</script>\r\n\r\n<div class=\"bg-indigo-100 p-4\">\r\n    <h1 class=\"text-4xl text-center\">Participantes</h1>\r\n    <p class=\"text-center m-auto mt-8\">Realiza la búsqueda de participantes por nombre, número de documento o por el correo electrónico institucional</p>\r\n    <form on:submit|preventDefault={submit} on:input={() => (sended = false)}>\r\n        <fieldset>\r\n            <div class=\"mt-4 flex flex-row\">\r\n                <Input label=\"Escriba el nombre, número de documento o el correo electrónico instiucional\" id=\"search_participante\" type=\"search\" class=\"mt-1 m-auto block flex-1\" bind:value={$form.search_participante} input$minLength=\"4\" autocomplete=\"off\" required />\r\n                <LoadingButton loading={sending} class=\"btn-indigo m-auto ml-1\" type=\"submit\">Buscar</LoadingButton>\r\n            </div>\r\n        </fieldset>\r\n    </form>\r\n\r\n    {#if sended}\r\n        <h1 class=\"mt-24 mb-8 text-center text-3xl\">Resultados de la búsqueda de participantes</h1>\r\n        <div class=\"bg-white rounded shadow\">\r\n            <table class=\"w-full whitespace-no-wrap table-fixed data-table\">\r\n                <thead>\r\n                    <tr class=\"text-left font-bold\">\r\n                        <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Nombre</th>\r\n                        <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Correo electrónico</th>\r\n                        <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Centro de formación</th>\r\n                        <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Regional</th>\r\n                        <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl text-center th-actions\">Acciones</th>\r\n                    </tr>\r\n                </thead>\r\n                <tbody>\r\n                    {#each resultados as resultado (resultado.id)}\r\n                        <tr class=\"hover:bg-gray-100 focus-within:bg-gray-100\">\r\n                            <td class=\"border-t\">\r\n                                <p class=\"px-6 py-4 flex items-center focus:text-indigo-500\">\r\n                                    {resultado.nombre}\r\n                                </p>\r\n                            </td>\r\n                            <td class=\"border-t\">\r\n                                <p class=\"px-6 py-4 flex items-center\">\r\n                                    {resultado.email}\r\n                                </p>\r\n                            </td>\r\n                            <td class=\"border-t\">\r\n                                <p class=\"px-6 py-4 flex items-center\">\r\n                                    {resultado.centro_formacion ? resultado.centro_formacion.nombre : ''}\r\n                                </p>\r\n                            </td>\r\n                            <td class=\"border-t\">\r\n                                <p class=\"px-6 py-4 flex items-center\">\r\n                                    {resultado.centro_formacion ? resultado.centro_formacion.regional.nombre : ''}\r\n                                </p>\r\n                            </td>\r\n                            <td class=\"border-t td-actions relative\">\r\n                                <DataTableMenu class={resultados.length < 4 ? 'z-50' : ''}>\r\n                                    <Item on:SMUI:action={() => showParticipante(resultado)}>\r\n                                        <Text>Vincular</Text>\r\n                                    </Item>\r\n                                </DataTableMenu>\r\n                            </td>\r\n                        </tr>\r\n                    {/each}\r\n\r\n                    {#if resultados.length === 0}\r\n                        <tr>\r\n                            <td class=\"border-t px-6 py-4\" colspan=\"4\">\r\n                                {$_('No data recorded')}\r\n                                <Button on:click={() => showRegister()} type=\"button\" variant={null}>Crear participante</Button>\r\n                            </td>\r\n                        </tr>\r\n                    {/if}\r\n                </tbody>\r\n            </table>\r\n        </div>\r\n    {/if}\r\n</div>\r\n\r\n<h1 class=\"mt-24 mb-8 text-center text-3xl\">Participantes vinculados</h1>\r\n<div class=\"bg-white rounded shadow\">\r\n    <table class=\"w-full whitespace-no-wrap table-fixed data-table\">\r\n        <thead>\r\n            <tr class=\"text-left font-bold\">\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Nombre</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Correo electrónico</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Centro de formación</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Regional</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Meses</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl w-full\">Horas</th>\r\n                <th class=\"px-6 pt-6 pb-4 sticky top-0 z-10 bg-white shadow-xl text-center th-actions\">Acciones</th>\r\n            </tr>\r\n        </thead>\r\n        <tbody>\r\n            {#each proyecto.participantes as participante (participante.id)}\r\n                <tr class=\"hover:bg-gray-100 focus-within:bg-gray-100\">\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center focus:text-indigo-500\">\r\n                            {participante.nombre}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center\">\r\n                            {participante.email}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center\">\r\n                            {participante.centro_formacion ? participante.centro_formacion.nombre : ''}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center\">\r\n                            {participante.centro_formacion ? participante.centro_formacion.regional.nombre : ''}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center\">\r\n                            {participante.pivot.cantidad_meses}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t\">\r\n                        <p class=\"px-6 py-4 flex items-center\">\r\n                            {participante.pivot.cantidad_horas}\r\n                        </p>\r\n                    </td>\r\n                    <td class=\"border-t td-actions relative\">\r\n                        <DataTableMenu class={proyecto.participantes.length < 4 ? 'z-50' : ''}>\r\n                            <Item on:SMUI:action={() => showParticipante(participante)}>\r\n                                <Text>Editar</Text>\r\n                            </Item>\r\n                            {#if authUser.id != participante.id}\r\n                                <Item on:SMUI:action={() => removeParticipante(participante.id)}>\r\n                                    <Text>Quitar</Text>\r\n                                </Item>\r\n                            {/if}\r\n                        </DataTableMenu>\r\n                    </td>\r\n                </tr>\r\n            {/each}\r\n\r\n            {#if proyecto.participantes.length === 0}\r\n                <tr>\r\n                    <td class=\"border-t px-6 py-4\" colspan=\"4\">{$_('No data recorded')}</td>\r\n                </tr>\r\n            {/if}\r\n        </tbody>\r\n    </table>\r\n</div>\r\n\r\n<!-- Dialog -->\r\n<Dialog bind:open={dialogOpen} id=\"participante\">\r\n    <div slot=\"title\">\r\n        <div class=\"mb-10 text-center\">\r\n            <div class=\"text-primary\">\r\n                {#if $formParticipante._method != null}\r\n                    Editar vinculación del participante: {dialogTitle}\r\n                {:else}\r\n                    Vincular participante: {dialogTitle}\r\n                {/if}\r\n            </div>\r\n        </div>\r\n    </div>\r\n    <div slot=\"content\">\r\n        <form on:submit|preventDefault={submitParticipante} id=\"participante-form\">\r\n            <fieldset>\r\n                <p class=\"block font-medium mb-2 text-gray-700 text-sm\">Por favor diligencie la siguiente información sobre la vinculación del participante.</p>\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de meses de vinculación\" id=\"cantidad_meses\" type=\"number\" input$step=\"0.5\" input$min=\"1\" input$max={proyecto.diff_meses} class=\"mt-1\" bind:value={$formParticipante.cantidad_meses} placeholder=\"Número de meses de vinculación\" autocomplete=\"off\" required />\r\n                </div>\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de horas semanales dedicadas para el desarrollo del proyecto\" id=\"cantidad_horas\" type=\"number\" input$step=\"1\" input$min=\"1\" class=\"mt-1\" bind:value={$formParticipante.cantidad_horas} placeholder=\"Número de horas semanales dedicadas para el desarrollo del proyecto\" autocomplete=\"off\" required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"rol_id\" value=\"Rol SENNOVA\" />\r\n                    <Select id=\"rol_id\" items={roles} bind:selectedValue={$formParticipante.rol_id} error={errors.rol_id} autocomplete=\"off\" placeholder=\"Seleccione un rol SENNOVA\" required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label class=\"mb-4\" labelFor=\"es_autor\" value=\"¿Es autor?\" />\r\n                    <select id=\"es_autor\" class=\"w-full border-gray-300 rounded-md shadow-sm focus:ring focus:ring-opacity-50 focus:border-indigo-200 focus:ring-indigo-200 p-4\" bind:value={$formParticipante.es_autor} required>\r\n                        <option value=\"1\" selected={$formParticipante.es_autor == 1 ? true : false}>Si</option>\r\n                        <option value=\"0\" selected={$formParticipante.es_autor == 0 ? true : false}>No</option>\r\n                    </select>\r\n                </div>\r\n            </fieldset>\r\n        </form>\r\n    </div>\r\n\r\n    <div slot=\"actions\" class=\"block flex w-full\">\r\n        <Button on:click={closeDialog} type=\"button\" variant={null}>\r\n            {$_('Cancel')}\r\n        </Button>\r\n        <LoadingButton loading={sending} class=\"btn-indigo ml-auto\" type=\"submit\" form={formID}>\r\n            {$_('Save')}\r\n        </LoadingButton>\r\n    </div>\r\n</Dialog>\r\n\r\n<!-- Dialog Register -->\r\n<Dialog bind:open={openNuevoParticipanteDialog} id=\"nuevo-participante\">\r\n    <div slot=\"title\">\r\n        <div class=\"mb-10 text-center\">\r\n            <div class=\"text-primary\">Registar nuevo participante</div>\r\n        </div>\r\n    </div>\r\n    <div slot=\"content\">\r\n        <form on:submit|preventDefault={submitRegister} id={formNuevoParticipanteId}>\r\n            <fieldset>\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Nombre completo\" id=\"nombre_nuevo_participante\" type=\"text\" class=\"mt-1\" bind:value={$formNuevoParticipante.nombre} error={errors.nombre} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Correo electrónico institucional\" id=\"email_nuevo_participante\" type=\"email\" class=\"mt-1\" bind:value={$formNuevoParticipante.email} error={errors.email} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"tipo_documento_nuevo_participante\" value=\"Tipo de documento\" />\r\n                    <Select id=\"tipo_documento_nuevo_participante\" items={tiposDocumento} bind:selectedValue={$formNuevoParticipante.tipo_documento} error={errors.tipo_documento} autocomplete=\"off\" placeholder=\"Seleccione un tipo de documento\" required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de documento\" id=\"numero_documento_nuevo_participante\" type=\"number\" input$min=\"0\" input$max=\"9999999999999\" class=\"mt-1\" bind:value={$formNuevoParticipante.numero_documento} error={errors.numero_documento} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de celular\" id=\"numero_celular_nuevo_participante\" type=\"number\" input$min=\"0\" input$max=\"9999999999\" class=\"mt-1\" bind:value={$formNuevoParticipante.numero_celular} error={errors.numero_celular} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"centro_formacion_id_nuevo_participante\" value=\"Centro de formación\" />\r\n                    <DynamicList id=\"centro_formacion_id_nuevo_participante\" bind:reset={openNuevoParticipanteDialog} bind:value={$formNuevoParticipante.centro_formacion_id} routeWebApi={route('web-api.centros-formacion')} placeholder=\"Busque por el nombre del centro de formación\" message={errors.centro_formacion_id} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"tipo_participacion_nuevo_participante\" value=\"Tipo de participación\" />\r\n                    <Select id=\"tipo_participacion_nuevo_participante\" items={tiposParticipacion} bind:selectedValue={$formNuevoParticipante.tipo_participacion} error={errors.tipo_participacion} autocomplete=\"off\" placeholder=\"Seleccione el tipo de participación\" required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"rol_id\" value=\"Rol SENNOVA\" />\r\n                    <Select id=\"rol_id\" items={roles} bind:selectedValue={$formNuevoParticipante.rol_id} error={errors.rol_id} autocomplete=\"off\" placeholder=\"Seleccione un rol SENNOVA\" required />\r\n                </div>\r\n\r\n                <p class=\"block font-medium mt-10 mb-10 text-gray-700 text-sm\">Por favor diligencie la siguiente información sobre la vinculación del participante.</p>\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de meses de vinculación\" id=\"cantidad_meses_nuevo_participante\" type=\"number\" input$step=\"0.5\" input$min=\"1\" input$max={proyecto.diff_meses} class=\"mt-1\" bind:value={$formNuevoParticipante.cantidad_meses} placeholder=\"Número de meses de vinculación\" autocomplete=\"off\" error={errors.cantidad_meses} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Input label=\"Número de horas semanales dedicadas para el desarrollo del proyecto\" id=\"cantidad_horas_nuevo_participante\" type=\"number\" input$step=\"1\" input$min=\"1\" class=\"mt-1\" bind:value={$formNuevoParticipante.cantidad_horas} placeholder=\"Número de horas semanales dedicadas para el desarrollo del proyecto\" autocomplete=\"off\" error={errors.cantidad_horas} required />\r\n                </div>\r\n\r\n                <div class=\"mt-4\">\r\n                    <Label required class=\"mb-4\" labelFor=\"es_autor\" value=\"¿Es autor?\" />\r\n                    <select id=\"es_autor\" class=\"w-full border-gray-300 rounded-md shadow-sm focus:ring focus:ring-opacity-50 focus:border-indigo-200 focus:ring-indigo-200 p-4\" bind:value={$formNuevoParticipante.es_autor} required>\r\n                        <option value=\"\">Seleccione una opción</option>\r\n                        <option value=\"1\" selected={$formNuevoParticipante.es_autor == 1 ? true : false}>Si</option>\r\n                        <option value=\"0\" selected={$formNuevoParticipante.es_autor == 0 ? true : false}>No</option>\r\n                    </select>\r\n                </div>\r\n            </fieldset>\r\n        </form>\r\n    </div>\r\n\r\n    <div slot=\"actions\" class=\"block flex w-full\">\r\n        <Button on:click={closeDialog} type=\"button\" variant={null}>\r\n            {$_('Cancel')}\r\n        </Button>\r\n        <LoadingButton loading={sending} class=\"btn-indigo ml-auto\" type=\"submit\" form={formNuevoParticipanteId}>\r\n            {$_('Save')}\r\n        </LoadingButton>\r\n    </div>\r\n</Dialog>\r\n\r\n<style>\r\n    :global(#nuevo-participante-dialog .mdc-dialog__surface, #participante-dialog .mdc-dialog__surface) {\r\n        max-width: 1050px;\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);
