___CSS_LOADER_EXPORT___.push([module.id, ".items .listItem{border-bottom:1px solid #ccc}.items .item{height:auto!important;line-height:1.6!important;text-overflow:clip!important;overflow:initial!important;white-space:break-spaces!important;padding-top:10px;padding-bottom:10px}.min-h .listContainer{min-height:50vh}", "",{"version":3,"sources":["webpack://./resources/js/Shared/Dropdowns/DynamicList.svelte"],"names":[],"mappings":"AA4EY,iBAAmB,4BACM,CAChC,aAEsB,qBACI,CACvB,yBAA2B,CAC3B,4BAAiC,CACjC,0BAA4B,CAC5B,kCAAoC,CACpC,gBAAiB,CACjB,mBAAoB,CACvB,sBAE+B,eACZ","sourcesContent":["<script>\r\n    import { afterUpdate, onMount } from 'svelte'\r\n    import axios from 'axios'\r\n    import Select from 'svelte-select'\r\n    import InputError from '@/Shared/InputError'\r\n    import { _ } from 'svelte-i18n'\r\n\r\n    export let classes = ''\r\n    export let id = ''\r\n    export let message\r\n    export let value\r\n    export let required\r\n    export let placeholder\r\n    export let routeWebApi\r\n    export let recurso\r\n    export let reset\r\n\r\n    let items = []\r\n    let itemFiltered = null\r\n    let select = null\r\n\r\n    $: if (routeWebApi) {\r\n        getItems()\r\n    }\r\n\r\n    onMount(() => {\r\n        getItems()\r\n        select = document.getElementById(id)\r\n    })\r\n\r\n    afterUpdate(() => {\r\n        if (required) {\r\n            itemFiltered != null && select != null ? select.setCustomValidity('') : select.setCustomValidity($_('Please fill out this field.'))\r\n        }\r\n    })\r\n\r\n    async function getItems() {\r\n        let res = await axios.get(routeWebApi)\r\n        items = res.data\r\n        selectItem()\r\n    }\r\n\r\n    function handleSelect(event) {\r\n        value = event.detail.value\r\n        recurso = event.detail\r\n    }\r\n\r\n    function selectItem() {\r\n        if (value) {\r\n            let filterItem = items.filter(function (item) {\r\n                return item.value == value\r\n            })\r\n            itemFiltered = filterItem[0]\r\n        }\r\n    }\r\n\r\n    $: if (reset) {\r\n        itemFiltered = null\r\n    }\r\n</script>\r\n\r\n<Select bind:selectedValue={itemFiltered} inputAttributes={{ id: id }} {placeholder} containerClasses=\"items w-full {classes}\" {items} on:select={handleSelect} on:clear={() => (value = null)} noOptionsMessage=\"No hay ítems, por favor revise los filtros o de clic en refrescar\" />\r\n{#if items.length == 0}\r\n    <div>\r\n        <p>Parece que no se han encontrado elementos, por favor haga clic en <strong>Refrescar</strong></p>\r\n        <button on:click={getItems} type=\"button\" class=\"flex underline\">\r\n            <svg xmlns=\"http://www.w3.org/2000/svg\" class=\"h-6 w-6 mr-2\" fill=\"none\" viewBox=\"0 0 24 24\" stroke=\"currentColor\">\r\n                <path stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\" d=\"M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15\" />\r\n            </svg>\r\n            Refrescar\r\n        </button>\r\n    </div>\r\n{/if}\r\n<InputError {message} />\r\n\r\n<style>\r\n    :global(.items .listItem) {\r\n        border-bottom: 1px solid #ccc;\r\n    }\r\n\r\n    :global(.items .item) {\r\n        height: auto !important;\r\n        line-height: 1.6 !important;\r\n        text-overflow: initial !important;\r\n        overflow: initial !important;\r\n        white-space: break-spaces !important;\r\n        padding-top: 10px;\r\n        padding-bottom: 10px;\r\n    }\r\n\r\n    :global(.min-h .listContainer) {\r\n        min-height: 50vh;\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);
